@import "../../../../variables.scss";

.hero-section {
	height: 100vh;
	background: url("../../../../assets/hero-img.png");
	background-position: center;
	background-size: cover;
	background-attachment: fixed;
	background-repeat: no-repeat;

	&__container {
		height: 100vh;
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
		position: relative;
	}

	&__tagline {
		text-align: center;
		color: $white;
	}

	&__name {
		font-size: 1.5rem;
		font-weight: 400;
		margin-bottom: 1rem;
	}

	&__oneliner {
		line-height: 1.25;
		span {
			color: $primary-color;
		}
	}

	&__socialIcons {
		position: absolute;
		bottom: 1.5rem;
		display: flex;
		align-items: center;
	}

	// Media Query
	@media only screen and (max-width: 460px) {
		&__name {
			font-size: 1rem;
		}

		&__oneliner {
			font-size: 3rem;
		}

		&__icon {
			width: 1rem;
			height: 1rem;
			margin: 0 0.7rem;
		}
	}
}
