.SectionHeading{position:relative;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;margin-bottom:2rem}.SectionHeading__title{display:inline-block;text-align:center;font-size:3rem;font-weight:700;color:#36b8ba;margin-bottom:1rem}.SectionHeading__zigzag{width:6rem;position:absolute;bottom:-30px}

