.card{width:17rem;box-shadow:0px 10px 20px 8px rgba(54,184,186,0.11);border-radius:0.8rem}.card__img{width:100%;height:13rem;background-position:center;background-size:cover;border-radius:0.8rem 0.8rem 0 0}.card__content{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:space-between;justify-content:space-between;padding:1rem;height:10.5rem;border-radius:0 0 0.8rem 0.8rem}.card__title{font-size:1.15rem;color:#022742}.card__description{font-size:0.8rem;color:#36b8ba}.card__brief{font-size:0.7rem;color:grey}

