.btn{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;border:none;cursor:pointer;border-radius:0.2rem;font-weight:600;font-size:0.8rem}.btn-icon{height:1rem;width:1rem;margin-right:0.5rem}.btn-label{width:100%;text-align:center}.btn-primary{background:#36b8ba;padding:0.4rem 0.8rem;color:#fff}.btn-secondary{background:#cafaee;padding:0.4rem 0.8rem;color:#36b8ba}.btn-secondary:hover{background-color:#aff8e6;transition:background-color 0.2s ease}.btn-large{background:#36b8ba;padding:0.6rem 1.5rem;color:#fff}.btn-primary:hover,.btn-large:hover{background-color:#31a1a3;transition:background-color 0.2s ease}

