.input-wrapper{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column}.label{color:#022742;font-weight:600;margin-bottom:0.4rem;font-size:0.9rem}.textfield{border-radius:0.25rem;padding:0.3rem 0.5rem;margin-bottom:0.8rem;outline:none;border:2px #36b8ba solid}.textfield::-webkit-input-placeholder{font-size:0.7rem}.textfield:-ms-input-placeholder{font-size:0.7rem}.textfield::placeholder{font-size:0.7rem}.textarea{border-radius:0.25rem;padding:0.3rem 0 0 0.5rem;border:2px #36b8ba solid;resize:none;outline:none}.textarea::-webkit-input-placeholder{text-align:left;font-size:0.7rem}.textarea:-ms-input-placeholder{text-align:left;font-size:0.7rem}.textarea::placeholder{text-align:left;font-size:0.7rem}

.contact{margin:8rem 0}.contact__heading{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;position:relative}.contact__title{text-align:center;font-size:3rem;font-weight:600;color:#36b8ba;margin-bottom:1rem}.contact__zigzag{width:8%;position:absolute;bottom:1rem}.contact__content{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:space-evenly;justify-content:space-evenly;grid-gap:2rem;gap:2rem}.contact__contactimg{width:25rem}.contact__form{width:20rem}.contact__form :nth-child(3){width:100%}.contact__form :nth-child(4){width:100%}.contact__button{margin-top:1.3rem}@media only screen and (max-width: 1000px){.contact__contactimg{width:18rem}}@media only screen and (max-width: 786px){.contact__contactimg{display:none}}.submit-btn{margin-top:1.5rem}.submit-btn :only-child{width:100%}

